.Contact {
    height: 50vh;
    padding-top: 10%;
}

.ContactContainer {
    height: 100%;
    display: flex;
    justify-content: space-between;
    margin: 0 6%;
    align-items: end;
}

.ContactContainer h2 {
    font-size: 4vw;
    font-weight: 450;
    margin-bottom: 0;
}

.ContactContainer h4 {
    font-size: 1.8vw;
    font-weight: 350;
    opacity: 0.5;
    margin-top: 4%;
}

.ContactRightContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 13%;
    margin-bottom: 5%;
}

.ContactIcons {
    display: flex;
    justify-content: end;
}

.ContactIcon {
    width: 15%;
    margin-left: 2%;
}